body {
  margin: 0;
  font-family: 'Arial', sans-serif;
}

.App {
  text-align: center;
  background-color: #121212; /* Dark background color */
  color: #fff; /* Light text color */
  min-height: 100vh;
}

.App-header {
  padding: 20px;
  background: linear-gradient(30deg, #ffdb01 0%, #ff851f 30%, #ff3d3d 52%, #ff0f6f 70%, #f0f 100%); /* Updated logo gradient */
  color: #121212; /* Dark text color */
}

.App-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 20px;
}

.blog-post {
  max-width: 800px;
  margin-bottom: 40px;
  padding: 30px;
  border: 1px solid #444; /* Slightly darker border color for each blog post */
  border-radius: 12px;
  background-color: #1a1a1a; /* Darker background color for blog post */
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2); /* Subtle box shadow for each post */
}

.blog-post h2 {
  color: #ffdb01; /* Hex theme color for blog post titles */
}

.post-date {
  color: #999; /* Light color for post date */
}

/* Navigation Bar */
nav {
  background-color: #121212;
  padding: 10px;
}

nav a {
  color: #fff;
  text-decoration: none;
  margin: 0 15px;
}

.hero-section {
  margin-bottom: 40px;
}

.cta-button {
  padding: 10px 20px;
  background-color: #ffdb01; /* Hex theme color for the button */
  color: #121212; /* Dark text color for the button */
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.cta-button:hover {
  background-color: #ff851f; /* Darker color on hover */
}

/* Footer */
footer {
  background-color: #121212;
  padding: 20px;
  color: #fff;
}

.footer-links {
  margin-top: 20px;
}

.footer-links a {
  color: #fff;
  text-decoration: none;
  margin-right: 20px;
}

.newsletter-subscription {
  margin-top: 20px;
  text-align: center;
}

.newsletter-subscription input[type="email"] {
  padding: 10px;
  margin-right: 10px;
  border: 1px solid #fff;
  border-radius: 4px;
  width: 200px;
}

.newsletter-subscription button {
  padding: 10px 20px;
  background-color: #ffdb01; /* Hex theme color for the button */
  color: #121212; /* Dark text color for the button */
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

.newsletter-subscription button:hover {
  background-color: #ff851f; /* Darker color on hover */
}

.social-media-integration {
  margin-top: 20px;
}

.social-media-integration a {
  display: inline-block;
  margin-right: 10px;
  color: #fff;
  text-decoration: none;
}

/* Add more specific styles for each social media icon if needed */
.social-media-integration i {
  font-size: 24px;
}

/* Updated CSS for FeaturedPosts */
.featured-posts {
  margin-top: 40px;
}

.featured-post {
  display: flex;
  align-items: center;
  border: 1px solid #444;
  border-radius: 12px;
  margin-bottom: 20px;
  overflow: hidden;
}

.featured-post img {
  width: 300px;
  height: 200px;
  object-fit: cover;
}

.post-details {
  padding: 20px;
}

.post-details h3 {
  color: #ffdb01; /* Hex theme color for post titles */
}

.post-date {
  color: #999; /* Light color for post date */
}



